	module.exports = {"data":{"profile":{"id":"92517619-b87a-5cf5-911c-bb3344ea6944","content":"<p><div class=\"content-block  text-center\"><p>Rosemary studied piano as a child and attended the Berklee College of Music on scholarship to study voice/music theory. While rocking through Boston&#8217;s night clubs with original bands she shared the stage with such acts as the Average White Band, Til Tuesday, Peter Noone, and Deborah Harry (Blondie). More recent notable gigs include benefit performances as a back up vocalist with the Super Group who opened up a show for the Father of Rock n&#8217; Roll, Chuck Berry, and included rock greats Alice Cooper, Ace Frehley (KISS), Rick Derringer, Will Lee (David Letterman show), Chuck Leavell (Allman Brothers, Rolling Stones), Elliot Easton (The Cars), Kasim Sulton (Todd Rundgren/Meatloaf), Chad Smith (Red Hot Chili Peppers), and Simon Kirke (Bad Company). She had a fun ski season gig at the Hotel Eden in the Swiss Alps and played at a Zurich jazz club. She has worked as a session vocalist on jingles and other recording projects.</p></div></p>\n<p><div class=\"content-block  text-center\"><p>For many years Rosemary was the bandleader of the 8 piece band Uptown Revival that performed at hundreds of weddings, private parties and corporate events. She has also performed with numerous other function bands. Although she started out singing rock and pop, in recent years she has been further exploring her love of jazz standards and the Great American songbook that she was exposed to while a student at Berklee. Her latest side ventures are singing with the C-Zone Jazz Group and an excellent Steely Dan tribute band called No Static.</p></div></p>\n","title":"Rosemary St. George","acf":{"title":"Vocalist","video":null,"facebook":"","instagram":"","testimonials":""},"image":{"localFile":{"childImageSharp":{"thumbnail":{"base64":"data:image/jpeg;base64,","aspectRatio":0.7490636704119851,"src":"/static/e20809bf867106fc2dc93f938332b6ab-6a5ba5d192951542644be52c3f2bc79c-06ad8.jpg","srcSet":"/static/e20809bf867106fc2dc93f938332b6ab-6a5ba5d192951542644be52c3f2bc79c-a24bd.jpg 110w,\n/static/e20809bf867106fc2dc93f938332b6ab-6a5ba5d192951542644be52c3f2bc79c-06ad8.jpg 200w","sizes":"(max-width: 200px) 100vw, 200px","originalImg":"/static/e20809bf867106fc2dc93f938332b6ab-6a5ba5d192951542644be52c3f2bc79c-06ad8.jpg"},"full":{"src":"/static/e20809bf867106fc2dc93f938332b6ab-6a5ba5d192951542644be52c3f2bc79c-edd6d.jpg","height":267,"width":200,"srcSet":"/static/e20809bf867106fc2dc93f938332b6ab-6a5ba5d192951542644be52c3f2bc79c-edd6d.jpg 1x"}}}},"yoast":{"title":"","canonical":"","metaDescription":"","linkdex":"","metakeywords":"","noIndex":"","noFollow":"","meta_robots_adv":"","metaKeywords":"","redirect":"","ogTitle":"","ogDescription":"","ogImage":"","twitterTitle":"","twitterDescription":"","twitterImage":""}},"site":{"id":"Site","siteMeta":{"title":"C-Zone Entertainment","subtitle":"Premiere DJs & Musicians | Servicing Greater Boston & New England | 617.320.0723"}}},"pathContext":{"id":"92517619-b87a-5cf5-911c-bb3344ea6944"}}

